.mai{
    display: flex;
    align-items: center;
    height: 100vh;
    justify-content: center;
}
.mai img{
    width: 28vw;
    padding: 1rem;
    margin-right: 3rem;
}
.light-mode {
    background-color: hsl(0, 0%, 98%);
    color: hsl(200, 15%, 8%);
  }
  
  .dark-mode {
    background-color: hsl(207, 26%, 17%);
    color: hsl(0, 0%, 100%);
  }
  .fle{
    display: flex;
    flex-direction: column;
    margin-top: 3.8rem;
    margin-left: 3rem;
  }
  
.mai p {
  font-weight: bold;
}
.not{
  font-weight: 100;
}
.mai div:nth-child(4) {
  position: absolute;
  flex-direction: column;
  bottom: 1rem;
  
}