@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap');
.search-area{
    padding: 2rem;
    
    
    
}
input[type="search"]::-webkit-search-cancel-button {
    -webkit-appearance: none;
  }
  .light-mode {
    background-color: hsl(0, 0%, 98%);
    color: hsl(200, 15%, 8%);
  }
  
  .dark-mode {
    background-color: hsl(207, 26%, 17%);
    color: hsl(0, 0%, 100%);
    
  }
#site-search{
    display: block;
    width: 20rem;
    height: 2rem;
    margin: 10px 0;
    padding: 10px;
    cursor: pointer;
    border-radius: 10px;
    border: 1px solid #eee;
    transition: .3s border-color;
    font-family: 'Space Grotesk', sans-serif;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;

}
.search-results{
    padding: .5rem;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    width: 30vw;
    text-align: center;
    
}
.search-results:hover{
  border: .2px solid rgb(126, 103, 103);
}
.form-field {
    position: relative;
    display: inline-flex;
  }
  
  .icon {
    position: absolute;
    top: 1rem;
    right: 10px;
  }
  .results{
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    width: fit-content;
    border-radius: 10px 10px 10px 10px;
    border: .5px solid #ccc;
    width: 18rem;
    height: 25rem;
    overflow: hidden;
    font-family: 'Nunito Sans', sans-serif;
    color: inherit;
  }
  .results:hover{
    box-shadow: 0 0 50px 15px #48abe0;
             

  }
  h3 span{
    font-size: medium;
    font-weight: 300;
    margin-left: 5px;
    
    

  }
  .info{
    padding: 1rem;
  }
  .results img{
    
    height: 13rem;
    width: -webkit-fill-available;
  }
  .main-results-area{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 2rem;
    padding: 1rem;
  }
  .pagination {
    display: inline-flex;
    margin-left: 40vw;
    width: 20rem;
    
  }
   li.page-number{
    text-decoration: none;
    list-style: none;
    border: 1px solid rgb(104, 76, 76);
    
  }
  .pagination a {
    color: inherit;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
  }

  li.page-number a:active {
    background-color: #4CAF50;
    color: white;
  }
  
  .pagination a:hover:not(.active) {background-color: #ddd;}

  select#region-select{
    display: block;
    width: 10rem;
    height: 2rem;
    margin: 10px 0;
    cursor: pointer;
    margin-left: 2rem;
    border-radius: 10px;
    text-align: center;
    border: 1px solid #eee;
    transition: .3s border-color;
    font-family: 'Space Grotesk', sans-serif;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;

  }
  option{
    position: absolute;
    top: 100%;
    right: 0;
    left: 0;
    z-index: 999;
    margin: 0 0;
    padding: 0 0;
    list-style: none;
    border: 1px solid #ccc;
    background-color: white;
    -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  }
  
  option li {
    padding: 0 6px;
    margin: 0 0;
    padding: 0 10px;
  }
  
  option li:hover {
    background-color: #39f;
    color: white;
  }